'use client';

import { useState, useEffect } from 'react';
import { useRequireAuth, useAuth } from '@/contexts/AuthContext';
import { parkingSlotService, analyticsService } from '@/lib/firebase-service';
import { ParkingSlot, DashboardStats } from '@/lib/types';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';

export default function MunicipalDashboard() {
  const { user, loading, authorized } = useRequireAuth(['Municipal']);
  const { signOut } = useAuth();
  const [parkingSlots, setParkingSlots] = useState<ParkingSlot[]>([]);
  const [stats, setStats] = useState<DashboardStats | null>(null);
  const [loadingData, setLoadingData] = useState(true);
  const [error, setError] = useState('');
  const [qrScannerOpen, setQrScannerOpen] = useState(false);
  const [scannedCode, setScannedCode] = useState('');

  useEffect(() => {
    if (authorized && user) {
      loadDashboardData();
    }
  }, [authorized, user]);

  const loadDashboardData = async () => {
    if (!user) return;
    
    try {
      setLoadingData(true);
      
      // Load parking slots for this municipal
      const slotsResponse = await parkingSlotService.getByOwnerId(user.uid);
      if (slotsResponse.success && slotsResponse.data) {
        setParkingSlots(slotsResponse.data);
      }

      // Load dashboard stats
      const statsResponse = await analyticsService.getDashboardStats(user.uid);
      if (statsResponse.success && statsResponse.data) {
        setStats(statsResponse.data);
      }
    } catch (err) {
      setError('Failed to load dashboard data');
      console.error('Error loading dashboard data:', err);
    } finally {
      setLoadingData(false);
    }
  };

  const handleSignOut = async () => {
    try {
      await signOut();
      window.location.href = '/login';
    } catch (error) {
      console.error('Error signing out:', error);
    }
  };

  const handleQRScan = async () => {
    if (!scannedCode.trim()) {
      setError('Please enter a QR code');
      return;
    }

    try {
      // Process QR code scan
      // This would integrate with the QR scanning service
      console.log('Processing QR code:', scannedCode);
      setScannedCode('');
      setQrScannerOpen(false);
      // Show success message or update UI accordingly
    } catch (err) {
      setError('Failed to process QR code');
    }
  };

  if (loading || !authorized) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
          <h2 className="text-xl font-semibold">Loading Municipal Dashboard...</h2>
        </div>
      </div>
    );
  }

  const dashboardImage = "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/6a565d9b-97bb-4d2c-8e4b-48fe62b64de1.png";
  const mapImage = "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/1387ee7a-3f72-4cbe-84c5-d459626efebd.png";

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="border-b bg-card">
        <div className="container mx-auto px-4 py-4 flex justify-between items-center">
          <div>
            <h1 className="text-2xl font-bold text-foreground">Municipal Dashboard</h1>
            <p className="text-muted-foreground">City Parking Management</p>
          </div>
          <div className="flex items-center space-x-4">
            <Dialog open={qrScannerOpen} onOpenChange={setQrScannerOpen}>
              <DialogTrigger asChild>
                <Button>QR Scanner</Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>QR Code Scanner</DialogTitle>
                  <DialogDescription>
                    Scan vehicle QR codes for entry/exit processing
                  </DialogDescription>
                </DialogHeader>
                <div className="space-y-4">
                  <div>
                    <Label htmlFor="qr-code">QR Code</Label>
                    <Input
                      id="qr-code"
                      placeholder="Enter or scan QR code"
                      value={scannedCode}
                      onChange={(e) => setScannedCode(e.target.value)}
                    />
                  </div>
                  <div className="flex space-x-2">
                    <Button onClick={handleQRScan} className="flex-1">
                      Process Entry/Exit
                    </Button>
                    <Button variant="outline" onClick={() => setQrScannerOpen(false)}>
                      Cancel
                    </Button>
                  </div>
                </div>
              </DialogContent>
            </Dialog>
            <span className="text-sm text-muted-foreground">Welcome, {user?.displayName}</span>
            <Button variant="outline" onClick={handleSignOut}>
              Sign Out
            </Button>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        {error && (
          <Alert variant="destructive" className="mb-6">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        {/* Hero Section */}
        <div className="mb-8">
          <img 
            src={dashboardImage}
            alt="Municipal parking management dashboard with city-wide overview and real-time monitoring"
            className="w-full h-48 object-cover rounded-lg mb-4"
            onError={(e) => {
              e.currentTarget.style.display = 'none';
            }}
          />
        </div>

        {/* Overview Stats */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">Total Parking Slots</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats?.totalParkingSlots || 0}</div>
              <p className="text-xs text-muted-foreground">
                {stats?.availableSlots || 0} Available, {stats?.occupiedSlots || 0} Occupied
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">Today's Traffic</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{(stats?.inboundToday || 0) + (stats?.outboundToday || 0)}</div>
              <p className="text-xs text-muted-foreground">
                {stats?.inboundToday || 0} In, {stats?.outboundToday || 0} Out
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">Daily Revenue</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">₱{(stats?.dailyRevenue || 0).toLocaleString()}</div>
              <p className="text-xs text-muted-foreground">
                Weekly: ₱{(stats?.weeklyRevenue || 0).toLocaleString()}
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">Active Reservations</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats?.activeReservations || 0}</div>
              <p className="text-xs text-muted-foreground">Current bookings</p>
            </CardContent>
          </Card>
        </div>

        {/* Main Content Tabs */}
        <Tabs defaultValue="overview" className="space-y-6">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="overview">City Overview</TabsTrigger>
            <TabsTrigger value="slots">Parking Slots</TabsTrigger>
            <TabsTrigger value="analytics">Analytics</TabsTrigger>
            <TabsTrigger value="settings">Settings</TabsTrigger>
          </TabsList>

          <TabsContent value="overview" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>City-wide Parking Map</CardTitle>
                <CardDescription>Real-time parking availability across the city</CardDescription>
              </CardHeader>
              <CardContent>
                <img 
                  src={mapImage}
                  alt="Interactive city parking map showing real-time availability and traffic flow"
                  className="w-full h-64 object-cover rounded-lg"
                  onError={(e) => {
                    e.currentTarget.style.display = 'none';
                  }}
                />
                <div className="mt-4 grid grid-cols-3 gap-4 text-sm">
                  <div className="text-center">
                    <div className="w-4 h-4 bg-green-500 rounded-full mx-auto mb-1"></div>
                    <p>Available ({stats?.availableSlots || 0})</p>
                  </div>
                  <div className="text-center">
                    <div className="w-4 h-4 bg-red-500 rounded-full mx-auto mb-1"></div>
                    <p>Occupied ({stats?.occupiedSlots || 0})</p>
                  </div>
                  <div className="text-center">
                    <div className="w-4 h-4 bg-yellow-500 rounded-full mx-auto mb-1"></div>
                    <p>Reserved ({stats?.activeReservations || 0})</p>
                  </div>
                </div>
              </CardContent>
            </Card>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <Card>
                <CardHeader>
                  <CardTitle>Recent Activity</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    <div className="flex justify-between items-center text-sm">
                      <span>Vehicle ABC-123 entered Slot A1</span>
                      <span className="text-muted-foreground">2 min ago</span>
                    </div>
                    <div className="flex justify-between items-center text-sm">
                      <span>Vehicle XYZ-789 exited Slot B5</span>
                      <span className="text-muted-foreground">5 min ago</span>
                    </div>
                    <div className="flex justify-between items-center text-sm">
                      <span>New reservation for Slot C3</span>
                      <span className="text-muted-foreground">10 min ago</span>
                    </div>
                  </div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Peak Hours Analysis</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    <div className="flex justify-between items-center">
                      <span className="text-sm">Morning Rush (7-9 AM)</span>
                      <Badge variant="secondary">85% Occupied</Badge>
                    </div>
                    <div className="flex justify-between items-center">
                      <span className="text-sm">Lunch Time (12-1 PM)</span>
                      <Badge variant="secondary">72% Occupied</Badge>
                    </div>
                    <div className="flex justify-between items-center">
                      <span className="text-sm">Evening Rush (5-7 PM)</span>
                      <Badge variant="secondary">90% Occupied</Badge>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          <TabsContent value="slots" className="space-y-4">
            <div className="flex justify-between items-center">
              <h2 className="text-xl font-semibold">Parking Slots Management</h2>
              <Button>Add New Slot</Button>
            </div>
            
            {loadingData ? (
              <div className="text-center py-8">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
                <p>Loading parking slots...</p>
              </div>
            ) : (
              <div className="grid gap-4">
                {parkingSlots.map((slot) => (
                  <Card key={slot.id}>
                    <CardHeader>
                      <div className="flex justify-between items-start">
                        <div>
                          <CardTitle className="text-lg">{slot.slotName}</CardTitle>
                          <CardDescription>{slot.location}</CardDescription>
                        </div>
                        <Badge variant={slot.isAvailable ? "default" : "secondary"}>
                          {slot.isAvailable ? 'Available' : 'Occupied'}
                        </Badge>
                      </div>
                    </CardHeader>
                    <CardContent>
                      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                        <div>
                          <p className="text-muted-foreground">Rate/Hour</p>
                          <p className="font-medium">₱{slot.ratePerHour}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Type</p>
                          <p className="font-medium">{slot.parkingType}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Reservations</p>
                          <p className="font-medium">{slot.reservationAllowed ? 'Allowed' : 'Not Allowed'}</p>
                        </div>
                        <div>
                          <p className="text-muted-foreground">Last Activity</p>
                          <p className="font-medium">
                            {slot.lastEntryTime ? slot.lastEntryTime.toLocaleTimeString() : 'None'}
                          </p>
                        </div>
                      </div>
                      <div className="flex space-x-2 mt-4">
                        <Button variant="outline" size="sm">Edit</Button>
                        <Button variant="outline" size="sm">View History</Button>
                        <Button variant="outline" size="sm">Update Pricing</Button>
                      </div>
                    </CardContent>
                  </Card>
                ))}
                
                {parkingSlots.length === 0 && (
                  <Card>
                    <CardContent className="text-center py-8">
                      <p className="text-muted-foreground">No parking slots found</p>
                      <Button className="mt-4">Create Your First Parking Slot</Button>
                    </CardContent>
                  </Card>
                )}
              </div>
            )}
          </TabsContent>

          <TabsContent value="analytics" className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <Card>
                <CardHeader>
                  <CardTitle>Revenue Trends</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    <div className="flex justify-between">
                      <span>Today</span>
                      <span className="font-medium">₱{(stats?.dailyRevenue || 0).toLocaleString()}</span>
                    </div>
                    <div className="flex justify-between">
                      <span>This Week</span>
                      <span className="font-medium">₱{(stats?.weeklyRevenue || 0).toLocaleString()}</span>
                    </div>
                    <div className="flex justify-between">
                      <span>This Month</span>
                      <span className="font-medium">₱{(stats?.monthlyRevenue || 0).toLocaleString()}</span>
                    </div>
                  </div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Occupancy Rate</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    <div className="flex justify-between">
                      <span>Current</span>
                      <span className="font-medium">
                        {stats?.totalParkingSlots ? 
                          Math.round((stats.occupiedSlots / stats.totalParkingSlots) * 100) : 0}%
                      </span>
                    </div>
                    <div className="flex justify-between">
                      <span>Average (Week)</span>
                      <span className="font-medium">68%</span>
                    </div>
                    <div className="flex justify-between">
                      <span>Peak Today</span>
                      <span className="font-medium">92%</span>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          <TabsContent value="settings" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Municipal Settings</CardTitle>
                <CardDescription>Configure your parking management preferences</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label>Default Parking Rate (₱/hour)</Label>
                  <Input type="number" placeholder="50" />
                </div>
                <div>
                  <Label>Maximum Reservation Duration (hours)</Label>
                  <Input type="number" placeholder="24" />
                </div>
                <div>
                  <Label>QR Scanner Timeout (seconds)</Label>
                  <Input type="number" placeholder="30" />
                </div>
                <Button>Save Settings</Button>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
